////
/// Key Info
/// @group components
////

.key-info {
    @include grid;
    margin-top: $measure-large;
    position: relative;

    @supports (display: flex) {
        justify-content: center;
    }
}

.key-info__item {
    @include grid-column-map(
        (
            "small": 6,
            "medium": 4,
            "large": 2
        )
    );

    @include media("above:large") {
        width: auto;
        padding-left:  $measure-large;
        padding-right: $measure-large;
    }

    // items 4+
    &:nth-child(1n+4) {
        @include media("below:large") {
            margin-top: $measure-large;
        }
    }

    // item 3
    &:nth-child(3) {
        @include media("below:medium") {
            margin-top: $measure-large;
        }
    }

    // items 2+
    &:nth-child(1n+2) {
        @include media("below:small") {
            margin-top: $measure-medium;
        }
    }
}

.key-info__item__prop {
    color: $color-emperor;
}

.key-info__item__value {
    color: $color-flamingo;
    font-size: 18px;
    font-weight: $font-weight-bold;
}

.key-info__item--feature {
    align-items: center;
    display: flex;

    .key-info__item__prop,
    .key-info__item__value {
        display: inline-block;
    }

    .key-info__item__prop {
        font-weight: $font-weight-bold;
        text-transform: uppercase;
        margin-right: $measure-small;
        color: lighten($color-emperor, 15%);
    }
}

.key-info.key-info--2-col {
    margin-top: 0;

    .key-info__item {
        @include grid-column-map(
            (
                "small": 6,
            )
        );

        @include media("below:medium") {
            margin-top: $measure-large;
        }

        // items 3+
        &:nth-child(1n+3) {
            @include media("above:small") {
                margin-top: $measure-large;
            }

            @include media("above:medium") {
                margin-top: $measure-enormous;
            }
        }
    }
}

.key-info.key-info--iconed {
    /// TODO: replace icon
    [class^='icon-'], [class*=' icon-'],
    .fa {
        display: inline-block;
        min-width: $measure-large;
        font-size: $font-size-delta;
    }

    .key-info__item__prop {
        span:not([class]) {
            vertical-align: top;
            line-height: $line-height-small;
        }
    }

    .key-info__item__value {
        padding-left: $measure-large;
    }
}

.key-info.key-info--dark-background {
    .key-info__item__prop,
    .key-info__item__value {
        color: $color-white;
    }
}

.key-info--dynamic {
    .no-js & {
        display: none;
    }
}
