////
/// Banner Half Width
/// @group components
////


.banner--hw {
    margin-bottom: 2 * $measure-large;

    $sm-text-column: 7;
    $sm-media-column: 5;

    .banner__text {
        align-self: center;

        @include grid-column-map(
            (
                "medium": $sm-text-column
            )
        );
    }

    .banner__text__breadcrumbs {
        margin-bottom: $measure-medium;

        @include media("below:medium") {
            margin-top: $measure-medium;
        }
    }

    .banner__media__img {
        overflow: hidden;
    }

    .banner__media {
        @include grid-column-map(
            (
                "medium": $sm-media-column
            )
        );

        img {
            @include media("above:medium") {
                max-width: none;
                width: auto;
                display: block;
                height: 100%;
                top: 0;
                left: 0;
            }
        }
    }

    .banner__cta {
        right: $measure-large;
        bottom: -$measure-medium;
    }

    &:not(.banner--hw--left) {
        .banner__text {
            align-self: center;

            @include grid-column-pull-map(
                (
                    "medium": $sm-media-column
                )
            );
        }

        .banner__media {
            @include grid-column-push-map(
                (
                    "medium": $sm-text-column
                )
            );
        }
    }
}

.banner--hw--left {
    .banner__cta {
        left: $measure-large;
        right: auto;
    }
}
