////
/// Banner Base
/// @group components
////


.banner {
    @include grid-top;
}

.banner__text {
    position: relative;

    .banner__text__subtitle {
        @extend %alpha;
        @include font-family(meta);
        color: $color-emperor;
        margin-top: (-$measure-medium);
        margin-bottom: $measure-large;
        font-weight: $font-weight-light;
    }

    p {
        @include font-family(meta);
        color: $color-emperor;
        font-size: $font-size-delta;
        font-weight: $font-weight-light;
    }
}

.banner__text.banner__text--dark-background {
    .banner__text__subtitle {
        color: $color-white;
    }

    p {
        color: $color-white;
        @include media("above:medium") {
            padding-right: $measure-large;
        }
    }
}

.banner__text__breadcrumbs {
    li {
        display: inline-block;

        &:not(:first-child) {
            //@extend .fa;
            //@extend .fa-chevron-right;
            /// TODO: replace iconfont
            @extend .icon-angle-right;

            margin-left: $measure-small;

            &:before {
                margin-right: $measure-small;
            }
        }
    }

}

.banner__media {
    position: relative;
}

.banner__cta {
    position: absolute;
    z-index: z-index(above);

    div + div {
        margin-top: $measure-medium;
    }
}
