.site-search {
    @include grid-column-base;

    text-align: right;
    padding-right: $grid-padding-half;

    @supports (display: flex) {
        align-self: center;
    }

    width: grid-column-width(1);

    @include media("below:between") {
        width: grid-column-width(2);
    }
}

.site-search__container__toggle-label {
    @include anchor(outline);
    cursor: pointer;

    padding: $measure-medium;
    @include media("above:medium") {
        padding: $measure-small;
    }

    @extend .icon-search;
    &:before {
        //@include font-family(icon);
        //content: $fa-var-search;
        /// TODO: replace iconfont

        font-size: $font-size-delta;
    }
}

.site-search__toggle,
.site-search__form {
    display: none;
}

.site-search__container {
    position: relative;
}

.site-search__form {
    position: absolute;
    right: 0;
    top: calc(100% + #{$measure-medium * 2.25});
    @include media("above:small") {
        top: calc(100% + #{$measure-small});
    }
    width: auto;
    height: $measure-xlarge;
    text-align: right;
    white-space: nowrap;
}

.site-search__button,
.site-search__input {
    vertical-align: top;
}

.site-search__input {
    margin: 0;
    padding: $measure-small;
    width: calc(#{$grid-width-min} - #{2 * $grid-padding + $measure-xlarge});
    height: $measure-xlarge;
}

.site-search__button {
    @extend .button--icon;
    width: $measure-xlarge;
    height: $measure-xlarge;


    //span:before {
    //    @include font-family(icon);
    //    content: $fa-var-search;
    //    font-size: $font-size-delta;
    //    height: 100%;
    //    line-height: 100%;
    //}
    /// TODO: replace icon font
    span {
        @extend .icon-search;
        font-size: $font-size-delta;
        height: 100%;
        line-height: 100%;
    }
}

.site-search__toggle:checked + .site-search__container {
    .site-search__form {
        display: block;
    }

    //.site-search__container__toggle-label:before {
    //    content: $fa-var-close;
    //}
    /// TODO: replace icon font
    .site-search__container__toggle-label {
        @extend .icon-close;
    }
}
