////
/// Text CTAs
/// @group components
////


/**
USED IN:
 - 361894 (DEV)

 */
.banner--text-ctas {
    @include grid;

    .banner__text {
        @include grid-column(6);
    }

    .banner__ctas {
        padding-right: $grid-padding-half;

        @include media("above:small") {
            margin-left: auto;
        }
        @include media("below:medium") {
            margin-right: auto;
        }

        @include media("below:small") {
            width: 100%;
            padding-left: $grid-padding-half;
        }
    }
}
