////
/// Sections
/// @group components
////


.section--lightgray {
    background-color: #f2f2f2;
}

.section--gallery {
    background-color: $color-gallery;
}

.section--prussian {
    background-image: linear-gradient(to right bottom, $color-prussian, $color-allports);
    color: $color-white;
}

.section--padded {
    padding-top: $measure-enormous;
    padding-bottom: $measure-enormous;
}

.section--margin {
    margin-top: $measure-enormous;
    margin-bottom: $measure-enormous;
}

.section--tmbp {
    margin-top: $measure-enormous;
    padding-bottom: $measure-enormous;
}

.section--centerheading {
    h2 {
        text-align: center;
    }
}

.section--angled-edge--drop {
    position: relative;
    z-index: 1;
    &:after {
        background: inherit;
        content: '';
        display: block;
        left: 0;
        position: absolute;
        right: 0;
        z-index: -1;
        -webkit-backface-visibility: hidden; // for Chrome Windows
        transform: skewY(3.5deg);
        transform-origin: 100%;
        height: 10%;
        @include media("above:medium") {
            height: 30%;
        }
        @include media("above:large") {
            height: 50%;
        }
    }
}

.section--angled-edge--raise {
    position: relative;
    z-index: 1;
    &:before {
        background: inherit;
        content: '';
        display: block;
        height: 50%;
        left: 0;
        position: absolute;
        right: 0;
        z-index: -1;
        -webkit-backface-visibility: hidden; // for Chrome Windows
        transform: skewY(-3deg);
        transform-origin: 100% 0;
        top: -30px;
        @include media("below:medium") {
            top: -60px;
        }
        @include media("above:medium") {
            top: -100px;
        }
    }
}


