////
/// Accordion
/// @group components
////

.js {
    .accordion__content__heading-anchor {
        &.inactive + div {
            @include visually-hidden;
        }
    }

    .accordion__content__heading {
        @include visually-hidden;
    }
}

.accordion__content__heading__icon {
    ///@extend .fa;
    /// TODO: replace iconfont

    float: right;

    .accordion__content__heading.inactive & {
        //@extend .fa-plus;
        /// TODO: replace iconfont
        @extend .icon-plus;
    }

    .accordion__content__heading.active & {
        //@extend .fa-minus;
        /// TODO: replace iconfont
        @extend .icon-minus;
    }
}

.accordion__content__heading-anchor {
    // @include anchor(activable); // TODO: This does nothing until added to anchor mixins

    h2 {
        margin: 0;
    }
}
