////
/// Secondary Navigation
/// @group components
////


.secondary-navigation {
    @include grid-base;
    background-color: $color-lightgray;
    display: inline-block;
    margin-top: $measure-large;
    border-width: $border-width-hairline 0;
    border-style: $border-style-default;
    border-color: $color-quill;

    @include media("below:extralarge") {
        width: calc(100% + #{$grid-padding * 2});
        margin-left:  -$grid-padding;
        margin-right: -$grid-padding;
    }

    @include media("below:medium") {
        display: none;
    }
}

.secondary-navigation__list {
    @include grid-column(12);
    @include reset-font-size("li");
    text-align: center;

    li {
        display: inline-block;
    }

    a {
        @include anchor(secondary-navigation);
    }
}
