.loader__header {
    position: relative;

    h2 {
        display: inline;
        font-weight: $font-weight-medium;
        font-size: $font-size-alpha;
    }
}

.loader--horizontal {
    animation-duration: 2s;
    animation-name: slidein;
    height: 4px;
    background-color: $color-alizarin;
    position: absolute;
    right: 0;
    width: 200%;
    margin-top: 10px;
}

@keyframes slidein {
    from {
        margin-left: 100%;
        width: 0%;
    }

    to {
        margin-left: 0%;
        width: 200%;
    }
}
