////
/// Wide Section
/// @group layout
////


.wide-column {
    @include grid-column-map(
        (
            "small": 12,
            "medium": 10,
            "large": 8
        )
    );

    @include grid-column-push-map(
        (
            "small": 0,
            "medium": 1,
            "large": 2
        )
    );
}
