////
/// Banner Full Width
/// @group components
////

.banner--fw {
    .banner__text {
        &:before {
            content: "";
            background: $color-white;
            width: 90%;
            height: #{$measure-large * 2};
            padding: $measure-medium;
            position: absolute;
            bottom: 100%;
            right: 0;
        }

        & > div {
            @include grid-column-map(
                (
                    "small": 8,
                    "medium": 6
                )
            );

            @include grid-column-push-map(
                (
                    "small": 4,
                    "medium": 3
                )
            );
        }
    }

    .banner__text__breadcrumbs {
        @include media("below:medium") {
            margin-top: $measure-medium;
            margin-bottom: $measure-large;
        }

        @include media("above:medium") {
            position: relative;
            top: -$measure-large;
        }
    }

    .banner__cta {
        right: $measure-large;
        bottom: $measure-large;
    }

    .banner__media {
        @include media("below:medium") {
            height: 200px;
            width: 100%;
            overflow-x: hidden;

            img {
                width: auto;
                max-width: auto;
                height: 100%;
                display: block;
                position: absolute;
            }
        }
    }
}

.banner--fw--left {
    .banner__text {
        &:before {
            right: auto;
            left: 0;
        }
    }

    .banner__cta {
        left: $measure-large;
        right: auto;
    }
}
