.ksp__items {  
  	display: flex;
  	align-items: center;
}

.ksp__item {
	text-align: center;
	padding: $measure-medium;	
	border-bottom: 2px #f2f2f2 solid;

	@include grid-column-map(
        (
            "small": 6,
            "medium": 3
        )
    );

	@include media("above:small") {
		border-bottom: none;
		border-right: 2px #f2f2f2 solid;
		&:last-child {
			border: none;
		}
	}

	@include media("below:medium") {
		&:nth-child(2) {
			border-right: none;
		}
	}
}


.ksp__item__figure {
		font-size: 70px;
		color: $color-alizarin;
		font-weight: lighter;
		margin: 0;
		line-height: 1;
}

.ksp__item__pre-figure {
  	color: $color-alizarin;
  	text-transform: uppercase;
  	margin: 0;
    font-weight: $font-weight-medium; 
}

.ksp__item__text {
		padding-top: 10px;
		color: $color-lucifer;
}

.ksp__source {
		text-align: center;
		width: 100%;
		
		p {
				margin: 0;
				font-style: italic;
				letter-spacing: 0.2px;
		}

		@include media("above:medium") {
		 	margin-top: 40px;
		  	margin-bottom: 60px;
		}
}
