.promo-card {
    z-index: 2;
    text-align: center;

    h2 {
        font-weight: $font-weight-medium;
        margin-top: 16px;
    }
}

.promo-card--left {

  @include media("above:medium") {
    width: 320px;
    box-shadow: 0 0 66px 3px rgba(0, 0, 0, 0.15);
    position: relative;
    float: right;
    top: -50%;
    right: 20%;
  }
}

.promo-card--right {
    position: relative;
    @include media("above:medium") {
      top: 60px;
      right: 10%;
    }
}

.promo-cta--button {
    padding: $measure-small $measure-medium;
    width: 160px;
    text-align: left;
    position: relative;

    .promo-cta--button--text {
        display: inline-block;
    }
    .promo-cta--button--icon {
        display: inline-block;
        position: absolute;
        right: $measure-medium;
        top: 12px;
    }
}

.promo-cta__white {
    background-color: white;
    padding: 10px 30px;

    h2:after {
        content: "";
        display: block;
        width: 80px;
        height: 3px;
        background-color: $color-alizarin;
        margin: 20px auto;
        max-height: 240px
    }
}

.promo-cta__blue {
    padding-left: 0;
    padding-right: 0;

    @include media("above:medium") {
        width: 500px;
    }

    h2:after {
        content: "";
        display: block;
        width: 80px;
        height: 3px;
        background-color: $color-picton;
        margin: 20px auto;
    }
}

