.programmes-cta__media {
  position: relative;
	&:before {
	  content: "";
	  position: absolute;
	  top: 0;
	  left: 0;
	  width: 100%;
	  height: 100%;
	  z-index: 1;
	  background-color: rgba(0,0,0,0.5);
  }
}

.programmes-cta__links {
	background-color: rgba(255, 255, 255, 0.95);
	margin: 60px 0;
	z-index: 2;
	position: relative;
	a {
		@include anchor(activable-stacked-arrow);
	}
}
