////
/// Media
/// @group components
////

.media,
.video-preview {
    width: 100%;
    height: auto;
    margin-bottom: $measure-medium;
    overflow: hidden;
    position: relative;

    // by default, assume 16:9
    &:not([class*="media--"]) {
        @extend %media--16-by-9;
    }

    figcaption {
        z-index: z-index(above);
    }
}

.media %media-elements,
.media %image-elements,
.video-preview %media-elements,
.video-preview %image-elements,
.video-preview a {
    @include absolute(top-full);
    height: 100%;
    // necessary for `iframe`
    margin-bottom: 0;
}

.media--vimeo,
.media--youtube {
    @extend %media--16-by-9;
}

// Build aspect ratio classes
@each $ratio, $percentage in $ratios {
    %img--#{nth($ratio, 1)}-by-#{nth($ratio, 2)} {
        height: $percentage;
    }
    %media--#{nth($ratio, 1)}-by-#{nth($ratio, 2)} {
        padding-top: $percentage;
    }
}


.video-preview {
    %media-elements,
    %image-elements {
        z-index: z-index(default);
    }

    a {
        @include anchor(video-preview);
        z-index: z-index(above);
    }
}

.video-preview--loading {
    %media-elements,
    %image-elements,
    a {
        @include visually-hidden;
    }
}
