@font-face {
	font-family: "iconfont";
	src: url('../fonts/iconfont/iconfont.eot');
	src: url('../fonts/iconfont/iconfont.eot?#iefix') format('eot'),
		url('../fonts/iconfont/iconfont.woff2') format('woff2'),
		url('../fonts/iconfont/iconfont.woff') format('woff'),
		url('../fonts/iconfont/iconfont.ttf') format('truetype'),
		url('../fonts/iconfont/iconfont.svg#iconfont') format('svg');
}



@mixin icon-styles($insert: before) {
    vertical-align: baseline;
    &:#{$insert} {
        font-family: "iconfont" !important;
            -webkit-font-smoothing: antialiased !important;
            -moz-osx-font-smoothing: grayscale !important;
        font-style: normal !important;
        font-variant: normal !important;
        font-weight: normal !important;
        // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
        text-decoration: none !important;
        text-transform: none !important;
        vertical-align: baseline !important;
    }
}

%icon-before {
	@include icon-styles(before);
}
%icon-after {
	@include icon-styles(after);
}


@function icon-char($filename) {
	$char: "";

	@if $filename == angle-right {
		$char: "\E001";
	}
	@if $filename == arrow-left {
		$char: "\E002";
	}
	@if $filename == arrow-right {
		$char: "\E003";
	}
	@if $filename == back-to-top {
		$char: "\E004";
	}
	@if $filename == calendar-check {
		$char: "\E005";
	}
	@if $filename == close {
		$char: "\E006";
	}
	@if $filename == facebook {
		$char: "\E007";
	}
	@if $filename == instagram {
		$char: "\E008";
	}
	@if $filename == linkedin {
		$char: "\E009";
	}
	@if $filename == menu {
		$char: "\E00A";
	}
	@if $filename == minus {
		$char: "\E00B";
	}
	@if $filename == plus {
		$char: "\E00C";
	}
	@if $filename == search {
		$char: "\E00D";
	}
	@if $filename == share {
		$char: "\E00E";
	}
	@if $filename == twitter {
		$char: "\E00F";
	}
	@if $filename == weibo {
		$char: "\E010";
	}
	@if $filename == youtube {
		$char: "\E011";
	}

	@return $char;
}

@mixin icon($filename, $insert: before, $extend: true) {
    @if $extend {
		@if $insert == "before" {
			@extend %icon-before;
		} @else {
			@extend %icon-after;
		}
    } @else {
        @include icon-styles;
    }
	&:#{$insert} {
		content: icon-char($filename) !important;
	}
}

