////
/// Breadcrumbs
/// @group components
////


.breadcrumbs {
    @include grid;
    font-size: $font-size-small;
    margin-top: $measure-enormous;
}

.breadcrumbs__list {
    @include grid-column(12);

    $normal-chevron-padding: $measure-medium;
    $mobile-chevron-padding: 3/4 * $measure-medium;

    li {
        display: inline-block;
        padding-top:    $border-width-hairline;
        padding-bottom: $border-width-hairline;
        padding-right: $normal-chevron-padding;
    }

    li + li {
        position: relative;


        &:before {
            @include font-family(icon);
            content: icon-char(angle-right);
            color: $color-lucifer;
            font-size: $font-size-default;
            padding-right: $normal-chevron-padding;
        }
    }

    @include media("below:small") {
        li {
            padding-right: $mobile-chevron-padding;
        }

        li + li {
            &:before {
                padding-right: $mobile-chevron-padding;
            }
        }
    }
}
