////
/// Nav Mobile
/// @group components
////


.nav-mobile {
    @include grid-column-base;
    font-size: $font-size-small;

    @supports (display: flex) {
        align-self: center;
    }

    @include media("above:medium") {
        display: none;
    }

    @include media("below:medium") {
        width: grid-column-width(2);
    }

    @include media("below:between") {
        width: grid-column-width(4);
    }
}

.nav-mobile__page-link,
.nav-mobile__close-link,
.nav-mobile__toggle-link {
    @include anchor(reset);
    @include anchor(nav-mobile-link);
}

.nav-mobile__close-link,
.nav-mobile__toggle-link {
    @include absolute(top-right);
    height: 100%;
}

.nav-mobile__page-link {
    margin-right: #{$measure-medium * 3};
}

.nav-mobile__close-link {
    span {
        //@extend .fa-close;
        /// TODO: replace iconfont
        @extend .icon-close;
    }
}

.nav-mobile__toggle-link {
    font-size: $font-size-small;

    span {
        //@extend .fa-plus;
        /// TODO: replace iconfont
        @extend .icon-plus;
    }

    .nav-mobile__level.open > .nav-mobile__level__header & span {
        //@extend .fa-minus;
        /// TODO: replace iconfont
        @extend .icon-minus;
    }
}

.nav-mobile__level--l0 > .nav-mobile__level__header {
    padding-top:    $measure-medium;
    padding-bottom: $measure-medium;
    border-bottom: $border-width-thick $border-style-default #e5e5e5;
}

.nav-mobile__level--l1 > .nav-mobile__level__header > .nav-mobile__page-link {
    padding: $measure-medium;
}





$level-colors:
    #ffffff,
    #ffffff,
    #ffffff,
    #f2f2f2,
    #e5e5e5,
    #d8d8d8,
    #cbcbcb,
    #bebebe,
    #a1a1a1;
@for $i from 1 through length($level-colors) {
    .nav-mobile__level--l#{$i - 1} {
        $color: nth($level-colors, $i);
        background-color: $color;
        color: contrasting-color($color);

        @if ($i > 2) {
            & > .nav-mobile__level__header {
                padding-left: $measure-medium;
            }
        }
    }
}

.nav-mobile__level--l0,
.nav-mobile__level--l1 {
    & > .nav-mobile__level__header {
        font-weight: $font-weight-medium;
    }
}

.nav-mobile__level--l1.open {
    & > .nav-mobile__level__header,
    & > ul {
        border-bottom: $border-width-hairline $border-style-default #a1a1a1;
    }
}

.nav-mobile__page-link--t2 {
    span {
        border-bottom: $border-width-default $border-style-default currentColor;
    }
}

.nav-mobile__level__header {
    position: relative;

}

.nav-mobile__level {
    ul:not([class]) {
        margin: 0 !important;
    }

    li {
        border-bottom: $border-width-hairline $border-style-default $color-gallery;
        position: relative;
    }

    &.nav-mobile__level--left {
        right: 100%;
        left: -100%;
    }
    &.nav-mobile__level--center {
        right: 0;
        left: 0;
    }
    &.nav-mobile__level--right {
        right: -100%;
        left: 100%;
    }

    &:not([class*="open"]) > ul {
        display: none;
    }
}

.nav-mobile__toggle {
    display: none;
}

.nav-mobile__content {
    @include animation(slide-right-out);
    background-color: $color-white;
    color: $color-black;
    width: 50%;
    min-width: $fixed-sidebar-width;
    height: 100%;
    align-self: flex-end;
    display: none;
    box-shadow: box-shadow(alpha);
    z-index: z-index(navigation);

    @supports (display: flex) {
        flex: 1 1 auto;
    }

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        @include animation(slide-right-in);
        display: block;
    }
}

.nav-mobile__toggle-label {
    @include non-selectable;
    background-color: $color-white;
    align-self: flex-end;
    display: inline-block;
    padding-top:    $measure-small;
    padding-bottom: $measure-small;
    border-width: 0;
    vertical-align: top;
    font-weight: $font-weight-bold;
    text-align: center;
    text-transform: uppercase;
    white-space: nowrap;

    @extend .icon-menu;
    &:before {
        //@include font-family(icon);
        //content: "#{$fa-var-bars} ";
        /// TODO: replace iconfont
        //vertical-align: middle;
        font-size: $font-size-gamma;
        margin-right: $measure-small;
        //font-weight: $font-weight-default;
    }

    &.open {
        @extend .icon-close;
        &:before {
            //content: "#{$fa-var-close} ";
            /// TODO: replace iconfont
        }
    }

    &:not([class*="open"]) {
        @include anchor(outline);
    }

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        padding: $measure-medium $measure-small;
        position: fixed;
        top: 0;
        right: $measure-medium;
        z-index: z-index(modal);
    }
}

.nav-mobile__close-label {
    background-color: rgba($color-black, opacity(half));
    width: 100%;
    height: 100%;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    z-index: z-index(above);

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        display: block;
    }
}

.nav-mobile__tertiary {
    background-color: $color-white;
    padding-top: $measure-small;
}


/** open state **/

.nav-mobile__overlay {
    .nav-mobile__toggle:checked + & {
        width: 100%;
        height: 100%;
        overflow-y: auto;
        position: fixed;
        top: 0;
        right: 0;
        z-index: z-index(above);
    }
}

.nav-mobile__container {
    @supports (display: flex) {
        height: 100%;
        display: flex;
        flex-direction: column;
    }

    .nav-mobile__toggle:checked + .nav-mobile__overlay & {
        width: 100%;
        height: 100%;
        overflow-y: auto;
        position: fixed;
        top: 0;
        left: 0;
    }
}
