.feature-news {
    position: relative;
    margin-top: 20px;

    @include media("above:medium") {
        margin: 60px 0 120px;
    }

    @include media("above:large") {
        margin: 120px 0 120px;
    }
}

.feature-news__cards {
    margin-top: 60px;
}

.feature-news__card {
    @include grid-column-map(
                    (
                            "small": 6,
                            "large": 6
                    )
    );
}

.feature-news__card:nth-child(2) {
    @include grid-column-map(
                    (
                            "small": 6,
                            "large": 3
                    )
    );
}

.feature-news__card:nth-child(3) {
    @include grid-column-map(
                    (
                            "small": 12,
                            "large": 3
                    )
    );
}

.feature-news__card--main,
.feature-news__card--follow {
    &:hover {
        box-shadow: 0 0 66px 3px rgba(0, 0, 0, 0.15);
    }
}

.feature-news__card--main {
    @include media("above:large") {
        position: relative;

        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            background-color: rgba(0, 0, 0, 0.5);
        }

        .feature-news__card__body {
            z-index: 2;
            color: white;
            position: absolute;
            bottom: 0;
            padding: 0 $measure-large;
            width: 100%;
        }

        img {
            width: 100%;
        }

        a {
            @include anchor(skate--dark-bg);
            font-size: $font-size-delta;
            font-weight: $font-weight-medium;
        }
    }
    @include media("below:large") {
        .feature-news__card__body {
            background-color: white;
            position: relative;
            top: -40px;
            width: 90%;
            padding: $measure-medium;
        }
        a {
            @include anchor(skate);
            font-weight: 600;
        }
    }
}

.feature-news__card--follow {
    img {
        width: 100%;
    }

    .feature-news__card__body {
        background-color: white;
        position: relative;
        top: -40px;
        width: 90%;
        padding: $measure-medium;
    }
}

.feature-news__card--follow,
.feature-news__card--latest,
.feature-news__card--more {
    a {
        @include anchor(skate);
        font-weight: 600;
    }
}

.feature-news__card--latest {
    padding-left: 20px;

    .feature-news__card__body {
        border-top: 1px solid $color-lucifer;

        @include media("above:medium") {
            padding: 20px 0 60px;
            border-bottom: 1px solid $color-lucifer;
        }

        &:before {
            content: "";
            display: block;
            width: 40px;
            height: 4px;
            background-color: $color-lucifer;
            position: relative;
            left: 0;
            top: -20px;
        }
    }
}

.feature-news__card--more {
    @include media("above:medium") {
        position: absolute;
        bottom: 0;
        left: 30px;
    }
}

.feature-news__card--main,
.feature-news__card--follow {
    .feature-news__card__body__title {
        &:after {
            content: "";
            display: block;
            width: 40px;
            height: 4px;
            background-color: $color-alizarin;
            position: relative;
            left: 0;
            top: 10px;
        }
    }
}

.feature-news__card--latest > .feature-news__card__body:not(.feature-news__card__body--slider-dots) {
    .feature-news__card__slider__item:not(:first-child) {
        display: none;
    }
}

.feature-news__card--latest > .feature-news__card__body.feature-news__card__body--slider-dots {
    position: relative;

    &:before {
        display: none;
    }

    .slick-dots {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 0;

        display: flex;
        flex-direction: row;

        > li {
            flex: 1;
            display: inline-block;
            background-color: transparent;
            height: auto;

            button {
                display: block;
                width: 100%;
                height: 4px;
                padding: 0;
                margin: 0;
                border: none;

                background-color: transparent;
                &:hover {
                    background-color: #aaaaaa;
                }
            }
            &.slick-active button {
                background-color: #666666;
            }
        }
    }
}